<template>
  <md-list
    class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
  >
    <md-subheader>Дневник</md-subheader>

    <md-list-item>
      <md-icon>view_headline</md-icon>
      <span class="md-list-item-text">Разделение предметов</span>
      <md-switch v-model="settings.diaryStripes" />
    </md-list-item>

    <md-list-item>
      <md-icon>view_week</md-icon>
      <span class="md-list-item-text">Суббота конец недели</span>
      <md-switch v-model="settings.noIncludeSunday" />
    </md-list-item>

    <md-list-item>
      <md-icon>access_time</md-icon>
      <span class="md-list-item-text">Ожидание нажатия</span>
      <md-menu md-direction="bottom-end" md-close-on-select>
        <md-button md-menu-trigger>{{ settings.diaryCountdown }}s</md-button>

        <md-menu-content>
          <md-menu-item @click="settings.diaryCountdown = 5">5s</md-menu-item>
          <md-menu-item @click="settings.diaryCountdown = 4">4s</md-menu-item>
          <md-menu-item @click="settings.diaryCountdown = 3">3s</md-menu-item>
          <md-menu-item @click="settings.diaryCountdown = 2">2s</md-menu-item>
          <md-menu-item @click="settings.diaryCountdown = 1">1s</md-menu-item>
        </md-menu-content>
      </md-menu>
    </md-list-item>
  </md-list>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  computed: mapState({
    settings: state => state.settings.storage
  }),
  methods: mapActions(["saveSettings"]),
  watch: {
    "settings.diaryStripes"(value) {
      this.saveSettings({
        name: "diaryStripes",
        value
      });
    },
    "settings.noIncludeSunday"(value) {
      this.saveSettings({
        name: "noIncludeSunday",
        value
      });
    },
    "settings.diaryCountdown"(value) {
      this.saveSettings({
        name: "diaryCountdown",
        value
      });
    }
  }
};
</script>
